      margin: 0;
      padding: 0;
      justify-content: flex-start;
      text-align: left;
    }
  
  .card-channel-logo{
    position: absolute;
    top: 5px;
    left:5px;
    border-radius: 50%;
    height: 45px;
    width:45px;
  }
  
  
  
    @media (max-width: 550px) {
      .card {
