.player-page {
  background-color: var(--color-primary);
  min-height: 45rem;
  margin-bottom: 2.8rem;
  padding: 0.5rem 0 3rem 0;
}

.player-card {
  margin: 0;
}

.actions {
  margin: 1rem 2rem;
  display: flex;
  justify-content: space-between;
}

.action-icon {
  width: 1.5rem;
  height: auto;
  vertical-align: middle;
  cursor: pointer;
}

.notLiked {
  color: var(--color-text);
}

.liked {
  color: var(--color-text);
}

.media-player{
    width: 100% !important;
    height: 203px !important;
}


.desc {
  margin: 0.5rem;
  display: flex;
  justify-content: space-between;
}

.desc-content {
  padding: 0.5rem 1rem;
}

.modal {
  display: none;
}

.open {
  position: fixed;
  z-index: 10;
  margin: 0;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  background-color: rgba(131, 125, 125, 0.4);
}

.modal-content {
  margin: auto;
  width: 85%;
  padding: 1rem 2rem;
  display: flex;
  flex-direction: column;
  border-radius: 1rem;
  background-color: black;
  color: white;
  /* background-color: var(--color-primary);
    color: var(--color-text); */
  box-shadow: rgba(255, 255, 255, 0.1) 0px 4px 12px;
}

.playlist-name {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 00.5rem;
  padding: 0 0.5rem;
}

.label {
  vertical-align: middle;
}

.modal-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 00.5rem 0;
}

.close-icon {
  height: 2rem;
  width: 2rem;
}

.modal-buttons {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  margin-top: 0.5rem;
}

.modal-buttons button {
  margin: 0 00.5rem;
  padding: 0.25rem 00.5rem;
}

.input-div {
  display: flex;
  justify-content: space-between;
  margin: 00.5rem;
}

.input-div label {
  vertical-align: middle;
}

.playlist-name-input {
  background-color: transparent;
  color: var(--color-text);
  border: 0;
  outline: 0;
  border: none;
  border-bottom: 1px solid var(--color-text);
  height: 1.5rem;
}

.close-btn {
  background-color: var(--color-text);
  color: var(--color-secondary);
  outline: none;
  border: 1px solid var(--color-text);
  border-radius: 00.5rem;
  margin: 1rem 0.25rem;
  padding: 00.25rem 1rem;
  cursor: pointer;
}

.action-btn {
  background-color: var(--color-text);
  color: var(--color-secondary);
  outline: none;
  border: 1px solid var(--color-text);
  border-radius: 00.5rem;
  margin: 00.5rem;
  padding: 00.25rem 1rem;
  cursor: pointer;
  vertical-align: middle;
}

@media only screen and (min-width: 768px) {
  .player-page {
    padding: 6rem 3rem 1rem 3rem;
    margin-bottom: 0;
    min-height: 100vh;
  }

  .media-player {
    width: 100% !important;
    height: 520px !important;
  }

  .actions {
    margin: 1rem 2rem;
    margin-right: 60rem;
  }

  .action-icon {
    width: 2rem;
    cursor: pointer;
  }

  .desc {
    margin: 0.5rem;
    margin-right: 70rem;
  }

  .modal-content {
    margin: auto 25rem;
  }
}

.like-page{
    background-color: var(--color-primary);
    min-height: 40rem;
    padding: 0.5rem 0 3rem 0;
}

.like-card{
    background-color: var(--color-primary);
    display: flex;
    flex-direction: column;
    margin-bottom: 2.8rem;
}

.add-video{
    text-align: center;
    margin: auto;
    color: var(--color-lightGray);
}

@media only screen and (min-width: 768px) {

    .like-page{
        min-height: 100vh;
    }

    .like-card{
        padding: 6rem 3rem 1rem 3rem;
        margin-bottom: 0;
        flex-direction: row;
        flex-wrap: wrap;
    }
}
.playlist-page{
    background-color: var(--color-primary);
    min-height: 40rem;
    padding: 0.5rem 0 3rem 0;
}

.container{
    color: var(--color-text);
    border-radius: 1rem;
    margin: 0.5rem;
    padding: 1rem;
    display: flex;
}

.trash-icon{
    width: 1.5rem;
    height: auto;
    vertical-align: middle;
    margin: 0 3.5rem;
    cursor: pointer;
}

.playlist-name{
    margin: 0 !important;
    color: var(--color-text);
    vertical-align: middle;
}

.playlist{
    margin-bottom: 1rem 0;
    display: flex;
    flex-direction: column;
}

.playlist-videos{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
}

@media only screen and (min-width: 768px) {

    .playlist-page{
        padding: 6rem 3rem 1rem 3rem;
        margin-bottom: 0;
        min-height: 100vh;
    }

    .playlist-videos{
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
    }

}
.watch-page{
    background-color: var(--color-primary);
    min-height: 40rem;
    padding: 0.5rem 0 3rem 0;
}

.watch-card{
    background-color: var(--color-primary);
    display: flex;
    flex-direction: column;
    margin-bottom: 2.8rem;
}

@media only screen and (min-width: 768px) {

    .watch-page{
        min-height: 100vh;
    }

    .watch-card{
        padding: 6rem 3rem 1rem 3rem;
        margin-bottom: 0;
        flex-direction: row;
        flex-wrap: wrap;
    }

}
.login-page{
    background-color: var(--color-primary);
    min-height: 100vh;
    display: flex;
    align-items: center;
}

.login-box{
    margin: auto;
    width: 80%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 1rem;
    border-radius: 1rem;
    box-shadow: rgba(255, 255, 255, 0.1) 0px 4px 12px;
}

.box-content{
    text-align: center;
}

.logo{
    font-size: 4rem;
    margin-bottom: 1rem !important;
    color: var(--color-secondary);
}

.login{
    font-size: 2rem;
    text-align: left !important;
    margin: 0 1rem;
    color: var(--color-text);
}


.login-input {
    border: 0;
    height: 1.5rem;
    width: 80%;
    outline: 0;
    font-size: 1rem;
    margin: 1rem;
    padding: 0.5rem 0.75rem;
    border-radius: 0.5rem;
    background-color: var(--color-text);
}

::-webkit-input-placeholder{
    color: var(--color-lightGray);
}

:-ms-input-placeholder{
    color: var(--color-lightGray);
}

::placeholder{
    color: var(--color-lightGray);
}

.btn{
    border: 0;
    width: 60%;
    outline: 0;
    font-size: 1.25rem;
    margin: 1rem;
    margin-bottom: 1.5rem !important;
    padding: 0.5rem;
    border-radius: 0.5rem;
    color: var(--color-primary);
    background-color: var(--color-text);
    cursor: pointer;
}

.btn:hover{
    background-color: var(--color-secondary-light);
    color: var(--color-text);
}

.sign-up{
    color: var(--color-secondary);
    text-decoration: none;
}

@media only screen and (min-width: 768px) {

    .login-box{
        width: 35%;
    }

}
